* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --color-bg: #fbfcfc;
  --color-surface: #fff;
  --color-border: #e1e4e8;
  --color-text: #24292f;
  --color-text-secondary: #57606a;
  --color-primary: #0969da;
  --color-primary-hover: #0550ae;
  --color-primary-disabled: oklch(55.4% .046 257.417);
  --color-code-bg: #f6f8fa;
  --color-success: #1a7f37;
  --color-success-bg: #dafbe1;
  --color-success-border: #4ac26b;
  --color-error: #d1242f;
  --color-error-bg: #ffebe9;
  --color-error-border: #f85149;
  --spacing-xs: .5rem;
  --spacing-sm: 1rem;
  --spacing-md: 1.5rem;
  --spacing-lg: 2rem;
  --spacing-xl: 3rem;
  --border-radius: 6px;
  --font-body: -apple-system, BlinkMacSystemFont, "Segoe UI", "Noto Sans", Helvetica, Arial, sans-serif;
  --font-mono: ui-monospace, SFMono-Regular, "SF Mono", Menlo, Consolas, "Liberation Mono", monospace;
}

:root[data-theme="dark"] {
  --color-bg: #22272e;
  --color-surface: #2d333b;
  --color-border: #444c56;
  --color-text: #adbac7;
  --color-text-secondary: #768390;
  --color-primary: #539bf5;
  --color-primary-hover: #6cb6ff;
  --color-code-bg: #1c2128;
  --color-success: #3fb950;
  --color-success-bg: #033a16;
  --color-success-border: #2ea043;
  --color-error: #f85149;
  --color-error-bg: #490202;
  --color-error-border: #da3633;
}

@media (prefers-color-scheme: dark) {
  :root:not([data-theme]) {
    --color-bg: #22272e;
    --color-surface: #2d333b;
    --color-border: #444c56;
    --color-text: #adbac7;
    --color-text-secondary: #768390;
    --color-primary: #539bf5;
    --color-primary-hover: #6cb6ff;
    --color-code-bg: #1c2128;
    --color-success: #3fb950;
    --color-success-bg: #033a16;
    --color-success-border: #2ea043;
    --color-error: #f85149;
    --color-error-bg: #490202;
    --color-error-border: #da3633;
  }
}

:root.theme-transition, :root.theme-transition *, :root.theme-transition :before, :root.theme-transition :after {
  transition: background-color .3s, border-color .3s, color .3s, box-shadow .3s !important;
}

@media (prefers-reduced-motion: reduce) {
  :root.theme-transition, :root.theme-transition * {
    transition: none !important;
  }
}

body {
  background: var(--color-bg);
  color: var(--color-text);
  font-family: var(--font-body);
  min-height: 100vh;
  line-height: 1.6;
  overflow-x: hidden;
}

header {
  padding: var(--spacing-md) var(--spacing-lg);
  background: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  width: auto;
  height: 36px;
  transition: filter .2s;
}

:root[data-theme="dark"] .logo {
  filter: invert();
}

header a {
  color: var(--color-text);
  align-items: center;
  text-decoration: none;
  transition: color .2s;
  display: flex;
}

header svg {
  width: 32px;
  height: 32px;
}

header svg path {
  fill: currentColor;
}

.theme-toggle-btn {
  width: 36px;
  height: 36px;
  color: var(--color-text);
  cursor: pointer;
  border-radius: var(--border-radius);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: background-color .2s, color .2s;
  display: flex;
}

.theme-toggle-btn:hover {
  background-color: var(--color-code-bg);
  color: var(--color-primary);
}

.theme-toggle-btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px #0969da4d;
}

:root[data-theme="dark"] .theme-toggle-btn:focus {
  box-shadow: 0 0 0 3px #539bf54d;
}

.theme-icon {
  width: 20px;
  height: 20px;
}

.theme-icon-light {
  width: 24px;
  height: 24px;
  display: none;
}

.theme-icon-dark, :root[data-theme="dark"] .theme-icon-light {
  display: block;
}

:root[data-theme="dark"] .theme-icon-dark {
  display: none;
}

main {
  max-width: 900px;
  padding: var(--spacing-xl) var(--spacing-md);
  margin: 0 auto;
}

main > h1 {
  text-align: center;
  margin-bottom: var(--spacing-xl);
  color: var(--color-text);
  font-size: 1.75rem;
  font-weight: 600;
}

section {
  margin-bottom: var(--spacing-lg);
}

.controls fieldset {
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  padding: var(--spacing-md);
  background: var(--color-surface);
}

.controls legend {
  color: var(--color-text);
  padding: 0 var(--spacing-xs);
  font-size: .875rem;
  font-weight: 600;
}

.format-options {
  gap: var(--spacing-md);
  margin-top: var(--spacing-sm);
  flex-wrap: wrap;
  display: flex;
}

.format-options label {
  cursor: pointer;
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius);
  align-items: center;
  transition: background-color .2s;
  display: flex;
}

.format-options label:hover {
  background-color: var(--color-code-bg);
}

.format-options input[type="radio"] {
  margin-right: var(--spacing-xs);
  cursor: pointer;
  width: 16px;
  height: 16px;
}

.format-options span {
  user-select: none;
  font-size: .9375rem;
}

.input-section label, .output-section label {
  margin-bottom: var(--spacing-xs);
  color: var(--color-text);
  font-size: .875rem;
  font-weight: 600;
  display: block;
}

#structure-input {
  width: 100%;
  min-height: 200px;
  padding: var(--spacing-md);
  font-family: var(--font-mono);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  background: var(--color-surface);
  color: var(--color-text);
  resize: vertical;
  font-size: .875rem;
  line-height: 1.5;
  transition: border-color .2s, box-shadow .2s;
}

#structure-input:focus {
  border-color: var(--color-primary);
  outline: none;
  box-shadow: 0 0 0 3px #0969da1a;
}

:root[data-theme="dark"] #structure-input:focus {
  box-shadow: 0 0 0 3px #539bf54d;
}

#structure-input::placeholder {
  color: var(--color-text-secondary);
  opacity: .6;
}

.action-section {
  justify-content: center;
  display: flex;
}

#transform-btn {
  padding: var(--spacing-sm) var(--spacing-xl);
  color: #fff;
  background: var(--color-primary);
  border-radius: var(--border-radius);
  cursor: pointer;
  border: none;
  font-size: 1rem;
  font-weight: 600;
  transition: background-color .2s, transform .1s;
  box-shadow: 0 1px 3px #0000001f;
}

#transform-btn:hover {
  background: var(--color-primary-hover);
}

#transform-btn:disabled {
  cursor: not-allowed;
  background: var(--color-primary-disabled);
}

#transform-btn:active {
  transform: translateY(1px);
}

#transform-btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px #0969da4d;
}

#output-display {
  width: 100%;
  min-height: 300px;
  padding: var(--spacing-md);
  background: var(--color-code-bg);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  margin: 0;
  overflow-x: auto;
}

#output-code {
  font-family: var(--font-mono);
  color: var(--color-text);
  white-space: pre;
  font-size: .875rem;
  line-height: 1.6;
  display: block;
}

.output-container {
  width: 100%;
  position: relative;
}

.copy-btn {
  top: var(--spacing-sm);
  right: var(--spacing-sm);
  background: var(--color-bg);
  width: 36px;
  height: 36px;
  color: var(--color-text);
  cursor: pointer;
  border-radius: var(--border-radius);
  opacity: .8;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: background-color .2s, color .2s;
  display: flex;
  position: absolute;
}

.copy-btn:hover {
  background-color: var(--color-code-bg);
  color: var(--color-primary);
  opacity: 1;
}

.copy-btn:focus {
  outline: none;
  box-shadow: 0 0 0 3px #0969da4d;
}

.copy-icon {
  width: 20px;
  height: 20px;
}

@media (width <= 768px) {
  header {
    padding: var(--spacing-sm) var(--spacing-md);
  }

  #logo {
    height: 28px;
  }

  header svg {
    width: 28px;
    height: 28px;
  }

  main {
    padding: var(--spacing-lg) var(--spacing-sm);
  }

  main > h1 {
    margin-bottom: var(--spacing-lg);
    font-size: 1.5rem;
  }

  .format-options {
    gap: var(--spacing-sm);
  }

  #structure-input {
    min-height: 150px;
  }

  #output-display {
    min-height: 200px;
  }

  #transform-btn {
    width: 100%;
  }
}

@media (width <= 480px) {
  .format-options {
    gap: var(--spacing-xs);
    flex-direction: column;
  }

  .format-options label {
    padding: var(--spacing-sm);
    border: 1px solid var(--color-border);
  }
}

#notification-container {
  top: var(--spacing-md);
  right: var(--spacing-md);
  z-index: 1000;
  gap: var(--spacing-sm);
  pointer-events: none;
  flex-direction: column-reverse;
  max-width: 400px;
  display: flex;
  position: fixed;
}

.toast {
  align-items: flex-start;
  gap: var(--spacing-sm);
  padding: var(--spacing-md);
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius);
  pointer-events: auto;
  opacity: 0;
  border-left-width: 4px;
  transition: none;
  display: flex;
  transform: translateX(100%);
  box-shadow: 0 4px 12px #00000026;
}

.toast--visible {
  animation: .3s forwards toast-slide-in;
}

.toast--removing {
  animation: .3s forwards toast-slide-out;
}

@keyframes toast-slide-in {
  from {
    opacity: 0;
    transform: translateX(100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes toast-slide-out {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(100%);
  }
}

@media (prefers-reduced-motion: reduce) {
  .toast--visible {
    animation: .3s forwards toast-fade-in;
  }

  .toast--removing {
    animation: .3s forwards toast-fade-out;
  }

  @keyframes toast-fade-in {
    from {
      opacity: 0;
    }

    to {
      opacity: 1;
    }
  }

  @keyframes toast-fade-out {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
    }
  }
}

.toast--success {
  border-left-color: var(--color-success-border);
  background: var(--color-success-bg);
}

.toast--error {
  border-left-color: var(--color-error-border);
  background: var(--color-error-bg);
}

.toast__icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-top: 2px;
}

.toast--success .toast__icon {
  color: var(--color-success);
}

.toast--error .toast__icon {
  color: var(--color-error);
}

.toast__message {
  color: var(--color-text);
  overflow-wrap: break-word;
  flex: 1;
  max-height: 200px;
  font-size: .9375rem;
  line-height: 1.5;
  overflow-y: auto;
}

.toast__close {
  width: 24px;
  height: 24px;
  color: var(--color-text-secondary);
  cursor: pointer;
  border-radius: var(--border-radius);
  background: none;
  border: none;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: background-color .2s, color .2s;
  display: flex;
}

.toast__close:hover {
  background-color: var(--color-border);
  color: var(--color-text);
}

.toast__close:focus {
  box-shadow: 0 0 0 2px var(--color-primary);
  outline: none;
}

.toast__close svg {
  width: 16px;
  height: 16px;
}

@media (width <= 768px) {
  #notification-container {
    top: var(--spacing-sm);
    right: var(--spacing-sm);
  }
}

@media (width <= 480px) {
  .toast {
    padding: var(--spacing-sm);
  }

  .toast__message {
    font-size: .875rem;
  }
}
